<template>
  <div class="smartselect">
    <input type="text" class="value" @focus="focusHandler" @blur="blurHandler" :value="selectedOption" @input="inputHandler">
    <div class="options" v-show="expanded">
      <span class="option" v-for="option in options" :key="option.id" @click="clickHandler(option)">{{ option.value }}</span>
    </div>
  </div>
</template>

<script>
const data = [
  { id: 0, value: 'hello-0' },
  { id: 1, value: 'hello-1' },
  { id: 2, value: 'hello-2' },
  { id: 3, value: 'hello-3' },
  { id: 4, value: 'hello-4' },
  { id: 5, value: 'hello-5' },
  { id: 6, value: 'hello-6' },
  { id: 7, value: 'hello-7' },
  { id: 8, value: 'hello-8' },
  { id: 9, value: 'hello-9' },
];

export default {
  data() {
    return {
      options: data,
      selectedOption: data[0].value,
      expanded: false,
    };
  },
  methods: {
    clickHandler(option) {
      this.selectedOption = option.value;
    },
    focusHandler() {
      this.expanded = true;
    },
    blurHandler() {
      setTimeout(() => {
        this.expanded = false;
      }, 200);
    },
    inputHandler(e) {
      this.options = data.filter(({ value }) => value.indexOf(e.target.value) >= 0);
      this.selectedOption = e.target.value;
    },
  },
};
</script>

<style scoped>
.smartselect {
  font-size: 20px;
  display: flex;
  position: relative;
}

.smartselect .value {
  font-size: 20px;
  width: 300px;
  height: 30px;
  padding: 0;
  border: 1px solid black;
}

.smartselect .options {
  display: flex;
  flex-direction: column;
  border: 1px solid gray;
  width: 300px;

  position: absolute;
  top: 30px;
}

.smartselect .options .option {
  cursor: pointer;
}

.smartselect .options .option:hover {
  background-color: #f5f5dc;
}
</style>



